import axios from 'axios'
import qs from 'qs'
import { ElNotification } from 'element-plus'

// 创建axios实例
const api = axios.create({
  // 设置默认响应数据类型
  responseType: 'json'
})

// 请求拦截器
api.interceptors.request.use(config => {
  // 如果是get delete 请求，将params放入url
  if (config.method === 'GET' || config.method === 'DELETE') {
    // 序列化params
    config.data = qs.stringify(config.data)
  }
  return config
})

// 响应拦截器
api.interceptors.response.use(
  response => {
    if (response.config.notTransformResponse) {
      // 如果设置notTransformResponse，直接返回数据
      return Promise.resolve(response)
    }
    // 如果响应code是200
    if (response.data.code === 200) {
      // 返回数据
      return Promise.resolve(response.data)
    }

    if (!response.config.hideMessage) {
      // 如果响应code不是200
      // 全局提示错误信息
      ElNotification.error(response.data.msg)
    }

    return Promise.reject(response.data)
  },
  error => {
    if (!error.config.hideMessage) {
      // 如果响应code不是200
      // 全局提示错误信息
      ElNotification.error(error.data.msg)
    }
    return Promise.reject(error)
  }
)

export default api
